	module.exports = {"data":{"site":{"siteMetadata":{"startDate":"2018-06-06","lessons":8,"noClass":[4]}},"markdownRemark":{"html":"<p>The point of this homework is to work on add background images and adding and styling tables.</p>\n<h2>Getting Started</h2>\n<p>Download the starter file that contains</p>\n<ul>\n<li>1 html file</li>\n<li>1 css file</li>\n<li>1 images folder with 3 images in it.</li>\n</ul>\n<p>You will need to link the CSS to the HTML using the <code class=\"language-text\">&lt;link&gt;</code> tag. Once you connect the CSS you will notice that some of the styles have been pre written for you.</p>\n<h3>Things to do in this homework.</h3>\n<ol>\n<li>Add a <code class=\"language-text\">background</code> to the <code class=\"language-text\">header</code></li>\n<li>Use <code class=\"language-text\">flexbox</code> to align the navigation</li>\n<li>Create a table</li>\n<li>Style the table</li>\n<li>Add hover state to nav</li>\n<li>Add <code class=\"language-text\">background</code> to the <code class=\"language-text\">body</code></li>\n</ol>\n<h2>Adding background to Header</h2>\n<p>In the images folder you will find a image <code class=\"language-text\">header.png</code> this should be set as a background for the element with <code class=\"language-text\">.banner</code> class. You will need to set a height of <code class=\"language-text\">height: 200px</code> and set the border color to be <code class=\"language-text\">#FDB927</code>. Use flex box to center the <code class=\"language-text\">h1</code> in the center of the header. The <code class=\"language-text\">h1</code> will have a font size of <code class=\"language-text\">4em</code> and a <code class=\"language-text\">text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);</code></p>\n<h2>Nav Bar</h2>\n<p>The <code class=\"language-text\">.navbar</code> element will use <code class=\"language-text\">#83a6be</code> and <code class=\"language-text\">#415863</code> for the background and border. You will need to use <code class=\"language-text\">flexbox</code> to align the content on its own row and centered.</p>\n<p>When you <code class=\"language-text\">:hover</code> over the links you will need to add <code class=\"language-text\">flake.png</code> as a <code class=\"language-text\">background-image</code> that is centered within the <code class=\"language-text\">a</code>.</p>\n<h2>Table</h2>\n<p>Use this content to build a table with a class of <code class=\"language-text\">chart</code>. Make sure you use the <code class=\"language-text\">th</code> for the headers and <code class=\"language-text\">td</code> for the table data.</p>\n<hr>\n<pre>\nGSB Cities\nThe 2017 - 2018 Snow Season Normal\nAverage to Date\nThis Time Last Season\nNormal Seasons Average\nAll Time Season Snowfall Record\nSyracuse    76.3  59.8  67.4  123.8 192.1 inches (1992 - 1993)\nRochester   62.5  43.8  49.6  99.5  161.7 inches (1959 - 1960)\nBuffalo     51.6  50.1  33.9  94.7  199.4 inches (1976 - 1977)\nBinghamton  24.9  36.2  59.5  83.4  135.2 inches (2016 - 2017)\nAlbany      19.9  26.2  15.3  60.2  112.5 inches (1970 - 1971)\n</pre>\n<hr>\n<h2>Style the table</h2>\n<p>The table will have a border that is <code class=\"language-text\">#415863</code> and background that is <code class=\"language-text\">#ffffff</code>. Both the <code class=\"language-text\">th</code> and <code class=\"language-text\">td</code> will have a border on the bottom with this color <code class=\"language-text\">#415863</code> and padding of <code class=\"language-text\">.5em</code>. The <code class=\"language-text\">th</code> will have a background that is <code class=\"language-text\">#374a52</code> you will also need to update the text color.</p>\n<p>The first column of the table needs to be <code class=\"language-text\">120px</code> wide and the next 4 columns must be <code class=\"language-text\">140px</code>. The cell that is \"featured\" needs to be <code class=\"language-text\">#b11c3f</code>.</p>\n<h2>Adding background to the body</h2>\n<p>Last we need to add a background to the body of the page. Use <code class=\"language-text\">background.jpg</code>, it should be fixed and centered on the page. It should have a <code class=\"language-text\">background-size</code> of cover.</p>\n<p>To allow the content to be transparent to see the background of the page you will need to add <code class=\"language-text\">background-color: rgba(255, 255, 255, 0.7)</code> to the <code class=\"language-text\">.container</code>. To add the drop shadow add <code class=\"language-text\">box-shadow: 0 1px 10px rgba(0, 0, 0, 0.5)</code>.</p>","frontmatter":{"lesson":"Homework","title":"Lesson 7","lessonId":7,"downloads":{"Download_Stater_File":null,"Homework_Files":{"file":{"publicURL":"/static/homework7_yourname-19ca8f5e638221095124801462c77f26.zip"}}}}},"allMarkdownRemark":{"edges":[{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/7/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 7","lesson":"CSS Backgrounds"},"fields":{"slug":"/7/index.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/7/background.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 7","lesson":"Background"},"fields":{"slug":"/7/background.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/7/tables.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 7","lesson":"HTML Tables"},"fields":{"slug":"/7/tables.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/7/homework.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 7","lesson":"Homework"},"fields":{"slug":"/7/homework.html"}}}]}},"pathContext":{"slug":"/7/homework.html","title":"Lesson 7"}}

